import React from 'react';
import {connect} from "react-redux";
import {errorFetchUsers, followed, getUsers,} from "../../redux/reducer/users.reducer";
import {Users} from "./Users";
import {compose} from "redux";
import {withAuthRedirectComponent} from "../../hoc/withAuthRedirectComponent";
import {getUsersPhotos} from "../../selectors/users.selector";
import {withRouter} from "react-router-dom";

export class UsersContainer extends React.Component {



    onPageChanged = (p) => {
        this.props.getUsers(p, this.props.pageSize)
    }

    //LifeCycles

    componentDidMount() {
        if (this.props.users.length) return
        let params = this.props.currentPage
        if(this.props.match.params.page !== this.props.currentPage) params = +this.props.match.params.page
        this.props.getUsers(params, this.props.pageSize)
    }


    handleErrorReconnect = () => {
        this.props.errorFetchUsers('')
        this.props.getUsers(this.props.currentPage, this.props.pageSize)
    }

    render = () => {

        if(this.props.error){
            return <div>
                    <h3>{this.props.error}</h3>
                    <button onClick={this.handleErrorReconnect}>Попробовать еще раз</button>
                </div>
        }

        return <Users
            totalUserCount={this.props.totalUserCount}
            pageSize={this.props.pageSize}
            currentPage={this.props.currentPage}
            users={this.props.users}
            onPageChanged={this.onPageChanged}
            isFetching={this.props.isFetching}
            isFollowing={this.props.isFollowing}
            onFollowed={this.props.followed}
        />
    }
}


const mapStateToProps = (state) => ({
    totalUserCount: state.usersPage.totalUserCount,
    pageSize: state.usersPage.pageSize,
    currentPage: state.usersPage.currentPage,
    users: getUsersPhotos(state),
    isFetching: state.usersPage.isFetching,
    isFollowing: state.usersPage.isFollowing,
    error: state.usersPage.error
})


export default compose(
    withRouter,
    connect(mapStateToProps, {
        getUsers,
        followed,
        errorFetchUsers
    }),
    withAuthRedirectComponent,
)(UsersContainer)



